import { useState, useEffect } from "react";
import { Redirect } from "react-router-dom";
import { cartEmpty } from "./helper/cartHelper";
import { getmeToken, processPayment } from "./helper/paymentHelper";
import { createOrder } from "./helper/orderHelper";
import { isAuthenticated, signout } from "../auth/helper";

import DropIn from "braintree-web-drop-in-react";

const PaymentB = ({ products, reload = undefined, setReload = (f) => f }) => {
  const [info, setInfo] = useState({
    loading: false,
    success: false,
    clientToken: null,
    error: "",
    instance: {},
  });

  const userId = isAuthenticated && isAuthenticated().user.id;
  const token = isAuthenticated && isAuthenticated().token;

  const getToken = (userId, token) => {
    getmeToken(userId, token)
      .then((info) => {
        if (info.error) {
          setInfo({ ...info, error: info.error });
          signout(() => <Redirect to="/" />);
        } else {
          const clientToken = info.clientToken;
          console.log(clientToken);
          setInfo({ clientToken });
          console.log(info.clientToken);
        }
      })
      .catch((err) => console.log(err));
  };
  useEffect(() => {
    getToken(userId, token);
  }, []);

  const getAmount = () => {
    let amount = 0;
    products.map((p) => {
      amount += parseFloat(p.price);
    });
    return amount;
  };
  const onPurchase = () => {
    setInfo({ loading: true });
    let nonce;
    let getNonce = info.instance
      .requestPaymentMethod()
      .then((data) => {
        console.log("MYDATA", data);
        nonce = data.nonce;
        const paymentData = {
          paymentMethodNonce: nonce,
          amount: getAmount(),
        };
        processPayment(userId, token, paymentData)
          .then((response) => {
            console.log("POINT-1", response);
            if (response.error) {
              if (response.code == "1") {
                console.log("PAYMENT FAILED");
                signout(() => <Redirect to="/" />);
              }
            } else {
              setInfo({ ...info, success: response.success, loading: false });
              console.log("PAYMENT SUCCESS");
              let product_names = "";
              products.forEach((item) => {
                product_names += item.name + ", ";
              });
              console.log(product_names);
              const orderData = {
                products: product_names,
                transaction_id: response.transaction.id,
                amount: response.transaction.amount,
              };
              console.log(userId, token, orderData);
              createOrder(userId, token, orderData)
                .then((response) => {
                  if (response.error) {
                    if (response.code == "1") {
                      console.log("ORDER FAILED");
                    }
                    signout(() => <Redirect to="/" />);
                  } else {
                    if (response.success == true) {
                      console.log("ORDER PLACED");
                    }
                  }
                })
                .catch((error) => {
                  setInfo({ loading: false, success: false });
                  console.log("ORDER FAILED", error);
                });
              cartEmpty(() => console.log("DID WE GET A CRASH?"));
              setReload(!reload);
            }
          })
          .catch((e) => console.log(e));
      })
      .catch((err) => console.log("NONCE", err));
  };
  const showBtnDropIn = () => {
    return (
      <div>
        {info.clientToken !== null && products.length > 0 ? (
          <div>
            <DropIn
              options={{ authorization: info.clientToken }}
              onInstance={(instance) => (info.instance = instance)}
            ></DropIn>
            <button onClick={onPurchase} className="btn btn-block btn-success">
              Buy Now
            </button>
          </div>
        ) : (
          <h3>Please login first or add something in cart</h3>
        )}
      </div>
    );
  };
  return (
    <div>
      <h3>Your bill is $ {getAmount()}</h3>
      {showBtnDropIn()}
    </div>
  );
};

export default PaymentB;
